import React, { useState } from "react";
import "./App.css";

function App() {
  const [input, setInput] = useState("");
  const [result, setResult] = useState(null);

  function handleButtonClick(value) {
    setInput((prevInput) => prevInput + value);
  }

  function calculateResult() {
    try {
      // eslint-disable-next-line
      const calculatedResult = eval(input);
      setResult(calculatedResult);
    } catch (error) {
      setResult("Error");
    }
  }

  function reset() {
    setInput("");
    setResult(null);
  }

  return (
    <div className="App">
      <form className="calculator">
        <div className="buttons">
          <div className="value">
            <p>{input || "0"}</p>
            <p>{result !== null ? `= ${result}` : ""}</p>
          </div>
          <span className="btn clear" onClick={reset}>
            Clear
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick("/");
            }}
          >
            /
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick("*");
            }}
          >
            *
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(7);
            }}
          >
            7
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(8);
            }}
          >
            8
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(9);
            }}
          >
            9
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick("-");
            }}
          >
            -
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(4);
            }}
          >
            4
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(5);
            }}
          >
            5
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(6);
            }}
          >
            6
          </span>
          <span
            className="btn plus"
            onClick={() => {
              handleButtonClick("+");
            }}
          >
            +
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(1);
            }}
          >
            1
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(2);
            }}
          >
            2
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(3);
            }}
          >
            3
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(0);
            }}
          >
            0
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick("00");
            }}
          >
            00
          </span>
          <span
            className="btn"
            onClick={() => {
              handleButtonClick(".");
            }}
          >
            .
          </span>
          <span className="btn equal" onClick={calculateResult}>
            =
          </span>
          <span className="btn developer">
            Developed by mohammed osama alkhatib
          </span>
        </div>
      </form>
    </div>
  );
}

export default App;
